import axios from 'axios';
export default function getPieclass() {
  var colors = ['#FD6969', '#EFC87C', '#38E7A7', '#2396FC', '#00897B', '#0288D1'];
  return {
    tooltip: {
        trigger: 'item',
        formatter: "{a} <br/>{b}: {c} ({d}%)"
    },
    // color: colors,
    legend: {
        orient: 'vertical',
        x: 'left',
        data:['1','1','','3','','']
    },
    series: [
        {
            name:'',
            type:'pie',
            radius: ['50%', '70%'],
            avoidLabelOverlap: false,
            label: {
                normal: {
                    position: 'center',
                    formatter: 'VIP\r\n',
                    textStyle: {
                      color: 'black',
                      fontSize: 24
                    }
                  },
                emphasis: {
                    show: false,
                    textStyle: {
                        fontSize: '30',
                        fontWeight: 'bold'
                    }
                }
            },
            data:[
                {value:1, name:'1'},
                {value:1, name:'3'},
                {value:2, name:''},
                {value:3, name:''},
                {value:5, name:'1'},
                {value:13, name:''},
            ]
        }
    ]
  };
}



